import { Box, Card, CardContent, CardHeader, Chip, Paper, Stack, Typography } from '@mui/material';

import { IProject } from '../../data/projects';
import React from 'react';
import StyledTypography from '../StyledTypography';

const Project = ({ title, technologies, description }: IProject) => {
  return (
    <>
      <Card variant='outlined'>
        <CardHeader
          title={<StyledTypography fontVariant="titleLight" capitalized={true}>
            {title}
          </StyledTypography>
          }
          titleTypographyProps={{
            variant: 'body1',
          }}
        />
        <CardContent sx={{ py: 0 }}>
          <Stack spacing={1}>
            <Typography variant='body2'>{description}</Typography>
            <Box sx={{
              display: 'flex',
              flexWrap: 'wrap',
            }}>
              {technologies.map((tech) => (<Paper
                key={tech.name}
                elevation={0}
                sx={{
                  m: 1 / 2,
                  borderRadius: 5,
                  display: 'flex',
                  backgroundColor:
                    theme => theme.palette.mode === "light"
                      ? theme.palette.grey[100]
                      : theme.palette.grey[900],
                }}>
                <Stack
                  direction="row"
                  spacing={1}
                  alignItems="center"
                  sx={{
                    py: 1 / 2,
                    px: 1,
                    minHeight: 36
                  }} >
                  {tech.icon}
                  <Typography variant='caption'>{tech.name}</Typography>
                </Stack></Paper>))}
            </Box>
          </Stack>
        </CardContent>
      </Card>
    </>
  );
};

export default Project;
