import { Box, Grid, Zoom } from '@mui/material';
import projects, { IProject } from '../../data/projects';

import Project from './Project';
import ProjectDetailsLayout from './ProjectDetailsLayout';
import React from 'react';

const WorkProjects = () => {
  return (
    <ProjectDetailsLayout title={<></>}>
      <Grid container
        spacing={2}
      >
        {projects.map((project: IProject, index: number) => (
          <Grid item
            md={12}
            lg={6}
            xl={4}
            key={`project-${index}`}
          >
            <Zoom in={true} timeout={500 + (index * 10)}>
              <Box>
                <Project key={`project-${index}`} {...project} />
              </Box>
            </Zoom>
          </Grid>
        ))}
      </Grid>
    </ProjectDetailsLayout >
  );
};

export default WorkProjects;
